@use '_mixin' as m;

.sidebar {
  padding: m.toRem(22) m.toRem(30) m.toRem(30) m.toRem(20);

  position: relative;

  display: flex;
  flex-direction: column;
  align-items: start;
  background-color: #fff;

  @media (min-width: m.toEm(1025)) {
    border-right: 1px solid #eaeaea;
  }
  @media (max-width: m.toEm(1024)) {
    padding: m.toRem(15) m.toRem(20) m.toRem(15) m.toRem(20);
    position: fixed;
    z-index: 1000;
    width: 100%;
    height: m.toRem(70);
    bottom: 0;
    left: 0;
    right: 0;

    border-radius: 30px 30px 0 0;
  }

  &__header {
    margin-bottom: m.toRem(22);
    @media (max-width: m.toEm(1024)) {
      display: none;
    }
  }
}

.nav {
  margin-bottom: m.toRem(20);
  display: flex;
  flex-direction: column;
  width: 100%;
  @media (max-width: m.toEm(1024)) {
    flex-direction: row;
    align-items: center;
    justify-content: stretch;
    justify-content: space-between;

    height: 100%;
  }
  &__title {
    margin-bottom: m.toRem(6);
    font-size: m.toRem(12);
    line-height: 145%;
    color: #9d9d9d;
    @media (max-width: m.toEm(1024)) {
      display: none;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: m.toRem(6);

    &:not(:last-child) {
      margin-bottom: m.toRem(30);
    }
    @media (max-width: m.toEm(1024)) {
      flex-direction: row;
      width: 100%;
      justify-content: space-evenly;
      gap: m.toRem(10);
    }
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: start;
  }

  &__link {
    padding-left: m.toRem(16);
    min-height: m.toRem(32);
    border-radius: 8px;
    width: 100%;
    display: flex;
    align-items: center;
    gap: m.toRem(6);
    font-size: m.toRem(14);
    line-height: 145%;
    background: transparent;
    transition: background 0.3s ease;
    position: relative;
    z-index: 100;
    &::before {
      content: '';
      width: m.toRem(16);
      height: m.toRem(16);
      @media (max-width: m.toEm(1024)) {
        width: m.toRem(20);
        height: m.toRem(20);
      }
    }

    &--pools::before {
      background: url('../img/svg/pools.svg') 0 0 / contain no-repeat;
    }
    &--pools.active::before {
      background: url('../img/svg/pools-active.svg') 0 0 / contain no-repeat;
    }

    &--protocols::before {
      background: url('../img/svg/protocols.svg') 0 0 / contain no-repeat;
    }
    &--protocols.active::before {
      background: url('../img/svg/protocols-active.svg') 0 0 / contain no-repeat;
    }

    &--coins::before {
      background: url('../img/svg/coin.svg') 0 0 / contain no-repeat;
    }

    &.active {
      pointer-events: none;
      background: #f2f4f7;
      font-weight: 500;
    }

    @media (any-hover: hover) {
      &:hover {
        background: #f2f4f7;
        .nav__link-icon {
          fill: var(--main-color);
          stroke: none;
        }
      }
    }
    @media (max-width: m.toEm(1024)) {
      padding: m.toRem(7) m.toRem(10);
      flex-direction: column;
      gap: m.toRem(3);
    }
  }

  &__link-text {
  }
}
