@use '_mixin' as m;

.options {
  display: flex;
  gap: m.toRem(10);

  // .options__item
  &__item {
    position: relative;
    cursor: pointer;
  }

  // .options__input
  &__input {
    width: 0;
    height: 0;
    opacity: 0;
    position: absolute;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;

    &:focus-visible + .options__label:before {
      outline: 3px dotted var(--main-color);
      outline-offset: 2px;
    }

    &:checked + .options__label:before {
      outline: 1px solid var(--main-color);
    }

    &:checked + .options__label:after {
      transform: scale(1);
    }
  }

  // .options__label
  &__label {
    display: flex;
    align-items: center;

    cursor: pointer;
    gap: m.toRem(6);
    font-size: m.toRem(14);
    // line-height: 110%;

    &:before {
      content: '';
      align-self: flex-start;
      flex-shrink: 0;
      width: m.toRem(16);
      height: m.toRem(16);
     
      background: var(--white);
      border-radius: 50%;
      outline: 1px solid var(--grey);
      transition: outline 0.3s ease 0s;
    }

    &:after {
      content: '';
      position: absolute;
      top: m.toRem(4);
      left: m.toRem(4);
      width: m.toRem(8);
      height: m.toRem(8);
      border-radius: 50%;
      background-color: var(--main-color);
      transform: scale(0);
      transition: transform 0.3s ease;
    }
  }
}
