@use '_mixin' as m;

body {
  background: #fff;
}

.wrapper {
  overflow: hidden;
  height: 100vh;
  min-height: 100%;
  display: grid;
  grid-template-columns: m.toRem(230) 1fr;
  background: #f6f7f8;

  @media (max-width: m.toEm(1700)) {
    grid-template-columns: m.toRem(210) 1fr;
  }

  @media (max-width: m.toEm(1024)) {
    display: flex;
    flex-direction: column;
    min-height: 100%;
  }
}

.page {
  overflow-y: auto;

  @media (max-width: m.toEm(992)) {
    padding-bottom: m.toRem(80);
    flex: 1 1 auto;
  }

  @media (max-width: m.toEm(576)) {
    padding-bottom: m.toRem(60);
  }

  &__container {
    padding: m.toRem(20) m.toRem(20);
    display: flex;
    flex-direction: column;
    min-height: 100%;
    width: 100%;
    @media (max-width: m.toEm(768)) {
      padding: m.toRem(20) m.toRem(15);
    }
  }
}

.main {
  padding-bottom: m.toRem(30);
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
}
[class*='__container'] {
  margin: 0 auto;
  // padding: 0 var(--container-offset);
  max-width: var(--container-width);
  width: 100%;
}
