@use '_mixin' as m;

.footer {
  padding: m.toRem(20) m.toRem(0);
  // .footer__container

  &__container {
  
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: m.toRem(30);
    color: #1f2226;
    font-size: m.toRem(14);
    line-height: 120%;
    @media (max-width: m.toEm(992)) {
      gap: m.toRem(20);
    }
  }

  // .footer__logo

  &__logo {
    margin-bottom: m.toRem(1);
  }

  // .footer__address

  &__address {
    flex-grow: 1;
  }

  // .footer__info

  &__links {
    margin-bottom: m.toRem(1);
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: m.toRem(20);
  }

  // .footer__copy

  &__copy {
    margin-right: m.toRem(10);
  }

  // .footer__link

  &__link {
    text-decoration: underline;
    text-decoration-skip-ink: none;
    text-align: right;
    @media (any-hover: hover) {
      &:hover {
        text-decoration: none;
      }
    }
  }
}
