@use '_mixin' as m;

.protocol {
  // .protocol__header

  &__header {
  }

  // .protocol__filters

  &__filters {
    margin-bottom: m.toRem(10);
    padding: m.toRem(12) m.toRem(20);
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: start;
    gap: m.toRem(20) m.toRem(40);
    background-color: #fff;
    border-radius: 6px;
    @media (max-width: m.toEm(992)) {
      padding: m.toRem(12) m.toRem(12);
      gap: m.toRem(20) m.toRem(20);
    }
  }

  // .protocol__metrics

  &__metrics {
    display: grid;
    grid-template-columns: minmax(m.toRem(350), m.toRem(541)) minmax(
        m.toRem(500),
        1fr
      );

    gap: m.toRem(10);
    @media (max-width: m.toEm(1280)) {
      display: flex;
      flex-direction: column;
    }
  }
}

.metrics {
  padding: m.toRem(20) m.toRem(20);
  width: 100%;
  border-radius: 6px;
  background: #fff;
  @media (max-width: m.toEm(992)) {
    padding: m.toRem(12) m.toRem(12);
  }

  // .metrics__header

  &__header {
    margin-bottom: m.toRem(20);
    display: flex;
    justify-content: space-between;
    gap: m.toRem(20);
    align-items: center;
  }

  // .metrics__protocol

  &__protocol {
    display: flex;
    align-items: center;
    gap: m.toRem(10);
  }

  // .metrics__protocol-icon

  &__protocol-icon {
    flex-shrink: 0;
    width: m.toRem(32);
    height: m.toRem(32);
  }

  // .metrics__protocol-name

  &__protocol-name {
    font-weight: 700;
    font-size: m.toRem(14);
  }

  // .metrics__actions

  &__actions {
  }

  // .metrics__pin-btn

  &__pin-btn {
  }

  // .metrics__pin-icon

  &__pin-icon {
    width: m.toRem(16);
    height: m.toRem(16);
  }

  // .metrics__tvl

  &__tvl {
    margin-bottom: m.toRem(15);
  }

  // .metrics__tvl-label

  &__tvl-label {
    display: block;
    margin-bottom: m.toRem(7);
    font-weight: 500;
    font-size: m.toRem(14);
  }

  // .metrics__tvl-bottom

  &__tvl-bottom {
    display: flex;
    align-items: center;
    gap: m.toRem(6);
  }

  // .metrics__tvl-value

  &__tvl-value {
    font-size: m.toRem(20);
    font-weight: 700;
  }

  // .metrics__tvl-arrow

  &__tvl-arrow {
    width: m.toRem(16);
    height: m.toRem(16);
  }

  // .metrics__section

  &__section {
  }

  // .metrics__section-title

  &__section-title {
    margin-bottom: m.toRem(8);
    font-weight: 700;
    font-size: m.toRem(14);
  }

  // .metrics__list

  &__list {
    display: flex;
    flex-direction: column;
  }

  // .metrics__item

  &__item {
  }
}
.item-metrics {
  font-size: m.toRem(14);
  border-bottom: 1px solid #dedede;
  @media (max-width: m.toEm(768)) {
    font-size: m.toRem(12);
  }

  &--open {
    .item-metrics__sublist {
      transition: max-height 0.2s ease;
      max-height: 500px;
    }
  }
  // .item-metrics__header

  &__header {
    padding: m.toRem(9) m.toRem(0);
    display: flex;
    align-items: center;
    gap: m.toRem(6);
    width: 100%;
  }

  // .item-metrics__label

  &__label {
  }

  // .item-metrics__arrow

  &__arrow {
    width: m.toRem(16);
    height: m.toRem(16);
  }

  // .item-metrics__value

  &__value {
    flex-grow: 1;
    text-align: end;
    span {
      color: #bababa;
    }
  }

  // .item-metrics__sublist

  &__sublist {
    max-height: 0;
    overflow: hidden;
    display: flex;
    flex-direction: column;

    transition: max-height 0.3s ease;
  }

  // .item-metrics__subitem

  &__subitem {
    padding: m.toRem(8) m.toRem(0);
    display: flex;
    justify-content: space-between;
    gap: m.toRem(7);
    font-size: m.toRem(12);
  }

  // .item-metrics__subitem-label

  &__subitem-label {
  }

  // .item-metrics__subitem-value

  &__subitem-value {
    flex-grow: 1;
    text-align: end;
  }
}

.chart-container {
  padding: m.toRem(20) m.toRem(20);
  display: flex;
  flex-direction: column;
  gap: m.toRem(20);
  grid-column: 2;
  grid-row: 1 / 3;
  border-radius: 6px;
  background: #fff;
  @media (max-width: m.toEm(992)) {
    padding: m.toRem(12) m.toRem(12);
  }
}

.chart-placeholder {
  width: 100%;
  img {
    width: 100%;
  }
}

.protocol-info {
  padding: m.toRem(20) m.toRem(20);
  display: flex;

  flex-direction: column;
  gap: m.toRem(20);
  width: 100%;
  border-radius: 6px;
  background: #fff;

  // .protocol-info__title

  &__title {
    font-weight: 600;
    font-size: m.toRem(16);
  }

  // .protocol-info__text

  &__text {
    line-height: 130%;
    font-size: m.toRem(14);
  }

  // .protocol-info__links

  &__links {
    display: flex;
    flex-wrap: wrap;
    justify-content: start;
    gap: m.toRem(10);
  }

  // .protocol-info__link

  &__link {
    padding: m.toRem(9) m.toRem(16);
    display: flex;
    align-items: center;
    gap: m.toRem(6);
    border: 1px solid #262626;
    border-radius: 100px;
    transition: background 0.3s ease 0s;
    @media (any-hover: hover) {
      &:hover {
        background-color: #f2f2f2;
      }
    }
  }

  // .protocol-info__link-icon

  &__link-icon {
    width: m.toRem(16);
    height: m.toRem(16);
  }

  // .protocol-info__link-text

  &__link-text {
    font-size: m.toRem(14);
  }
}

.chart-controls {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  gap: m.toRem(10);

  // .chart-controls__left

  &__left {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    gap: m.toRem(10);
  }

  // .chart-controls__right

  &__right {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;

    gap: m.toRem(10);
  }
}
.add-metrics {
  // .add-metrics__btn

  &__btn {
    padding: m.toRem(9) m.toRem(16);
    display: flex;
    align-items: center;
    gap: m.toRem(6);
  }

  // .add-metrics__text

  &__text {
    color: #bababa;
    font-size: m.toRem(14);
  }

  // .add-metrics__icon

  &__icon {
    flex-shrink: 0;
    width: m.toRem(16);
    height: m.toRem(16);
  }
}

.metrics-tags {
  display: flex;
  align-items: center;
  gap: m.toRem(5);

  // .metrics-tags__item

  &__item {
    padding: m.toRem(9) m.toRem(16);
    display: flex;
    align-items: center;
    gap: m.toRem(5);
    border: 1px solid #262626;
    border-radius: 100px;
    @media (max-width: m.toEm(768)) {
      padding: m.toRem(9) m.toRem(9);
    }
  }

  // .metrics-tags__text

  &__text {
    font-size: m.toRem(14);
  }

  &__close {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  // .metrics-tags__icon

  &__close-icon {
    flex-shrink: 0;
    width: m.toRem(16);
    height: m.toRem(16);
  }
}
.switch-currency {
  padding: m.toRem(5) m.toRem(16);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: m.toRem(6);

  // .switch-currency__item

  &__item {
    padding: m.toRem(4) m.toRem(0);
    position: relative;
    cursor: pointer;
  }

  // .switch-currency__input

  &__input {
    width: 0;
    height: 0;
    opacity: 0;
    position: absolute;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    pointer-events: none;

    &:focus-visible + .switch-currency__label {
      outline: 3px dotted var(--main-color);
      outline-offset: 2px;
    }

    &:checked + .switch-currency__label {
      color: var(--main-color);
    }

    &:checked + .options__label:after {
      transform: scale(1);
    }
  }

  // .switch-currency__label

  &__label {
    cursor: pointer;
    font-size: m.toRem(14);
    color: #bababa;
  }
}
.chart-code {
  padding: m.toRem(10) m.toRem(10);
  display: flex;
  align-items: center;
  justify-content: center;
  // .chart-code__icon

  &__icon {
    flex-shrink: 0;
    width: m.toRem(16);
    height: m.toRem(16);
  }
}
.chart-download {
  padding: m.toRem(10) m.toRem(10);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: m.toRem(6);

  // .chart-download__icon

  &__icon {
    flex-shrink: 0;
    width: m.toRem(16);
    height: m.toRem(16);
  }

  // .chart-download__text

  &__text {
    font-size: m.toRem(14);
  }
}

.has-border {
  border: 1px solid #dedede;
  background: #fff;
  border-radius: 6px;
  transition: background 0.3s ease 0s;
  @media (any-hover: hover) {
    &:hover {
      background-color: #faf8f8;
    }
  }
}
