@use '_mixin' as m;

.pools {
  // .pools__body

  &__body {
  }

  &__header {
    margin-bottom: m.toRem(10);
    padding: m.toRem(12) m.toRem(20);
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: m.toRem(20) m.toRem(40);
    background-color: #fff;
    border-radius: 6px;
    @media (max-width: m.toEm(992)) {
      padding: m.toRem(12) m.toRem(12);
      gap: m.toRem(20) m.toRem(20);
    }
  }

  &__banner {
    display: flex;
    justify-content: center;
    width: 100%;
  }

  // .pools__title

  &__title {
    font-weight: 700;
    font-size: m.toRem(20);
    line-height: 110%;
  }

  // .pools__filters

  &__filters {
  }
}

.pool {
  display: flex;

  align-items: center;
  overflow: hidden;

  @media (max-width: m.toEm(768)) {
    align-items: start;
  }

  // .pool__items

  &__items {
    display: flex;
    align-items: center;
    flex-shrink: 0;
  }

  // .pool__logo

  &__logo {
    flex-shrink: 0;
    width: m.toRem(32);
    height: m.toRem(32);
    border-radius: 50%;
    overflow: hidden;

    &--last {
      position: relative;
      left: m.toRem(-10);
    }
    @media (max-width: m.toEm(768)) {
      width: m.toRem(20);
      height: m.toRem(20);
    }
  }

  // .pool__info

  &__info {
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: m.toRem(5);
  }

  // .pool__name

  &__name {
    font-weight: 700;
    text-transform: uppercase;

    span {
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  // .pool__fee

  &__fee {
    padding: m.toRem(3) m.toRem(6);
    font-weight: 700;
    font-size: m.toRem(10);
    line-height: 100%;
    text-align: center;
    color: var(--white);
    background-color: var(--main-color);
    border-radius: 100px;
  }
}

.table {
  &--scroll {
    @media (max-width: m.toEm(992)) {
      overflow: auto;
      margin-left: m.toRem(-20);
      margin-right: m.toRem(-20);

      padding-left: m.toRem(20);
      padding-right: m.toRem(20);

      &::-webkit-scrollbar {
        display: none;
      }
    }
  }

  &__body {
    background: var(--white);
    border-radius: 6px;

    &--scroll {
      @media (max-width: m.toEm(992)) {
        width: m.toRem(900);
      }
    }
  }

  &__row {
    border-bottom: 1px solid #ebebeb;
  }

  &__items {
    min-height: m.toRem(59);
    display: grid;
    position: relative;
    grid-template-columns:
      minmax(m.toRem(200), m.toRem(400)) minmax(m.toRem(100), m.toRem(300))
      minmax(m.toRem(100), m.toRem(300)) minmax(m.toRem(100), m.toRem(300)) minmax(
        m.toRem(100),
        m.toRem(300)
      )
      minmax(m.toRem(90), m.toRem(117));
    align-items: center;
    font-size: m.toRem(14);

    color: #0c0c0c;

    @media (max-width: m.toEm(1400)) {
      font-size: m.toRem(13);
    }

    @media (max-width: m.toEm(768)) {
      grid-template-columns:
        minmax(m.toRem(100), m.toRem(395)) minmax(m.toRem(80), m.toRem(246))
        minmax(m.toRem(80), m.toRem(255));
    }
    @media (max-width: m.toEm(359)) {
      font-size: m.toRem(11);
    }

    &--title {
      min-height: m.toRem(47);
      font-size: m.toRem(12);
      font-weight: 500;
      color: var(--main-color);

      @media (max-width: m.toEm(359)) {
        font-size: m.toRem(11);
      }
    }

    &--protocols {
      grid-template-columns:
        minmax(m.toRem(120), m.toRem(180)) minmax(m.toRem(70), m.toRem(120))
        minmax(m.toRem(100), m.toRem(120)) minmax(m.toRem(70), m.toRem(120))
        minmax(m.toRem(70), m.toRem(120))
        minmax(m.toRem(70), m.toRem(120))
        minmax(m.toRem(100), m.toRem(180))
        minmax(m.toRem(100), m.toRem(700));
    }
    &--coins-market {
      grid-template-columns:
        minmax(m.toRem(130), m.toRem(270)) minmax(m.toRem(120), m.toRem(220))
        minmax(m.toRem(80), m.toRem(160)) minmax(m.toRem(80), m.toRem(160))
        minmax(m.toRem(80), m.toRem(160))
        minmax(m.toRem(80), m.toRem(160))
        minmax(m.toRem(80), m.toRem(160))
        minmax(m.toRem(80), m.toRem(160))
        minmax(m.toRem(80), m.toRem(160));
    }
    &--coins {
      grid-template-columns:
        minmax(m.toRem(130), m.toRem(270)) minmax(m.toRem(120), m.toRem(240))
        minmax(m.toRem(80), m.toRem(240)) minmax(m.toRem(80), m.toRem(240))
        minmax(m.toRem(80), m.toRem(240))
        minmax(m.toRem(80), m.toRem(240))
        minmax(m.toRem(80), m.toRem(240));
    }
  }

  &__item {
    padding: m.toRem(0) m.toRem(15);
    display: flex;
    align-items: center;
    height: 100%;
    &:not(:last-child) {
      border-right: 1px solid #ebebeb;
    }

    span {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    &.change-up {
      color: #3fb84f;
    }
    &.change-down {
      color: #e24a42;
    }

    @media (max-width: m.toEm(1280)) {
      padding: m.toRem(0) m.toRem(10);
    }
    @media (max-width: m.toEm(768)) {
      &.hidden-mob {
        display: none;
      }
    }
  }

  &__item-star {
    &:not(:last-child) {
      margin-right: m.toRem(20);
    }

    flex-shrink: 0;
    width: m.toRem(18);
    height: m.toRem(18);
    @media (max-width: m.toEm(1280)) {
      &:not(:last-child) {
        margin-right: m.toRem(10);
      }
    }
    @media (max-width: m.toEm(768)) {
      display: none;
    }
  }

  // .table__item-btn

  &__item-btn {
    padding: m.toRem(7) m.toRem(7);
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--main-color);
    transition: background 0.3s ease 0s;
    color: var(--white);
    font-weight: 500;
    font-size: m.toRem(12);
    line-height: 130%;
    border-radius: 6px;

    @media (any-hover: hover) {
      &:hover {
        background: #000000;
      }
    }
  }
}

.coins-item {
  display: flex;
  align-items: center;
  gap: m.toRem(6);
  overflow: hidden;
  text-overflow: ellipsis;

  // .coins-item__counter

  &__counter {
    flex-shrink: 0;
  }

  // .coins-item__logo

  &__logo {
    width: m.toRem(24);
    height: m.toRem(24);
  }

  // .coins-item__name

  &__name {
    line-height: 1.15;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

.chains {
  display: flex;
  align-items: center;

  // .chains__list

  &__list {
    display: flex;
    align-items: center;
  }

  // .chains__item

  &__item {
    width: m.toRem(24);
    height: m.toRem(24);

    border-radius: 50%;
    overflow: hidden;
    &:not(:first-child) {
      margin-left: m.toRem(-6);
    }
  }

  // .chains__icon

  &__icon {
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center;
  }

  // .chains__more

  &__more {
    margin-left: m.toRem(-6);
    width: m.toRem(24);
    height: m.toRem(24);
    border-radius: 50%;
    font-size: 10px;
    line-height: 100%;
    color: #fff;
    background: var(--main-color);
  }
}

.filters {
  flex-grow: 1;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;

  gap: m.toRem(20);

  // .filters__options

  &__options {
    display: flex;
    align-items: center;
    gap: m.toRem(12);
  }

  // .filters__star

  &__star {
    flex-shrink: 0;
    width: m.toRem(18);
    height: m.toRem(18);
  }

  &__items {
    margin-left: auto;
    display: inline-flex;
    flex-wrap: wrap;
    align-items: center;
    gap: m.toRem(10);
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: m.toRem(10);
  }

  &__download {
  }

  // .filters__item
  &__item {
    position: relative;

    &.active {
      .filters__arrow {
        transform: rotate(-90deg);
      }
    }
  }

  // .filters__btn

  &__btn {
    padding: m.toRem(9) m.toRem(14) m.toRem(9) m.toRem(16);
    width: 100%;
    min-width: m.toRem(90);
    min-height: m.toRem(36);
    display: flex;
    align-items: center;
    gap: m.toRem(7);
    justify-content: space-between;
    background: var(--white);
    border: 1px solid var(--grey);
    border-radius: 6px;
    overflow: hidden;
    text-overflow: ellipsis;
    transition: background 0.3s ease 0s;
    @media (any-hover: hover) {
      &:hover {
        background-color: #faf8f8;
      }
    }

    @media (max-width: m.toEm(768)) {
      padding: m.toRem(9) m.toRem(10);
    }
  }

  // .filters__title

  &__title {
    transition: color 0.3s ease 0s;
    pointer-events: none;
    font-size: m.toRem(14);
    white-space: nowrap;

    color: #bababa;

    @media (max-width: m.toEm(768)) {
      font-size: m.toRem(12);
    }
  }

  // .filters__icon

  &__icon {
    flex-shrink: 0;
    pointer-events: none;
    width: m.toRem(16);
    height: m.toRem(16);
    transition: transform 0.3s ease 0s;

    @media (max-width: m.toEm(768)) {
      width: m.toRem(14);
      height: m.toRem(14);
    }

    .active & {
      transform: rotate(-180deg);
    }
  }
}

.dropdown-item {
  &.active {
    .dropdown-filter {
      opacity: 1;
      visibility: visible;
      transform: translate(0, 0);
    }
  }
}

.dropdown-filter {
  padding: m.toRem(10) 0;
  position: absolute;
  width: 100%;
  max-height: m.toRem(200);
  overflow: auto;
  z-index: 100;
  display: flex;
  flex-direction: column;
  gap: m.toRem(2);
  top: calc(100% + m.toRem(5));
  left: 0;
  background: var(--white);
  border: 1px solid var(--grey);
  border-radius: 10px;
  opacity: 0;
  visibility: hidden;
  transform: translate(0, m.toRem(-10));
  transition: opacity 0.3s ease;
  &__item {
    padding: m.toRem(10) m.toRem(20);
    cursor: pointer;
    transition: background 0.3s ease 0s;
    @media (any-hover: hover) {
      &:hover {
        background-color: var(--grey);
      }
    }
  }
}

.link-deposit {
  display: none;
  @media (max-width: m.toEm(768)) {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
  }
}
