@use '_mixin' as m;
.banner-earn {
  padding: m.toRem(21) m.toRem(36) m.toRem(28) m.toRem(26);
  display: flex;
  align-items: center;
  gap: m.toRem(15);
  width: 100%;
  max-width: m.toRem(760);
  background: #ffdd2d;
  border-radius: 6px;
  @media (max-width: m.toEm(1560)) {
    padding: m.toRem(20) m.toRem(20);
  }
  @media (max-width: m.toEm(768)) {
    flex-wrap: wrap;
    justify-content: center;
    padding: m.toRem(20) m.toRem(10);
  }

  // .banner-earn__logo

  &__logo {
    flex-shrink: 0;
    width: m.toRem(31);
    @media (max-width: m.toEm(768)) {
      width: m.toRem(25);
    }
  }

  // .banner-earn__info

  &__info {
    display: flex;
    flex-grow: 1;
    gap: m.toRem(25);
    @media (max-width: m.toEm(1560)) {
      gap: m.toRem(10);
      align-items: start;
    }
  }
  &__col {
    display: flex;
    flex-direction: column;
  }

  // .banner-earn__title

  &__title {
    margin-bottom: m.toRem(7);
    font-family: var(--second-family);
    font-weight: 900;
    font-size: m.toRem(24);
    line-height: 110%;
    color: #1f2226;
    @media (max-width: m.toEm(1560)) {
      font-size: m.toRem(20);
    }
  }

  // .banner-earn__text

  &__text {
    font-family: var(--second-family);
    font-weight: 700;
    font-size: m.toRem(16);
    line-height: 110%;
    color: #1f2226;
  }

  // .banner-earn__link

  &__link {
    padding: m.toRem(10) m.toRem(10);
    width: m.toRem(147);
    min-height: m.toRem(48);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 12px;
    background: #1f2226;
    font-weight: 350;
    font-size: m.toRem(16);
    color: #fff;
    transition: background 0.3s ease 0s;
    @media (any-hover: hover) {
      &:hover {
        background: #000000;
      }
    }
    @media (max-width: m.toEm(768)) {
      width: 100%;
    }
  }
}
