@use '_mixin' as m;

.header {
  margin-bottom: m.toRem(10);
  // .header__container

  &__container {
    display: flex;

    align-items: center;
    gap: m.toRem(15);
  }

  // .header__search

  &__search {
    position: relative;
    justify-self: center;
    width: 100%;
    max-width: m.toRem(480);
   
  }

  // .header__search-btn

  &__search-btn {
    position: absolute;
    left: m.toRem(16);
    top: 50%;
    transform: translate(0px, -50%);
    @media (max-width: m.toEm(768)) {
      left: m.toRem(12);
    }
  }

  // .header__search-icon

  &__search-icon {
    width: m.toRem(14);
    height: m.toRem(14);
  }

  // .header__search-input

  &__search-input {
    padding: m.toRem(12) m.toRem(12) m.toRem(14) m.toRem(42);
    display: flex;
    align-items: center;
    font-size: m.toRem(14);

    height: m.toRem(36);
    border-radius: 7px;
    font-size: m.toRem(14);
    box-shadow: 0 0 0 1px var(--grey);
    color: #bababa;
    background-color: #fff;
    overflow: hidden;
    text-overflow: ellipsis;
    transition: box-shadow 0.3s ease;
    &:focus {
      outline: none;
      box-shadow: 0 0 0 1px var(--main-color);
    }

    @media (max-width: m.toEm(768)) {
      padding: m.toRem(12) m.toRem(12) m.toRem(14) m.toRem(35);
    }
  }
  &__filter {
  }
  &__filter-btn {
    width: m.toRem(36);
    height: m.toRem(36);
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 0 0 1px var(--grey);
    border-radius: 6px;
    background-color: #fff;
  }
  &__filter-icon {
    width: m.toRem(20);
    height: m.toRem(20);
  }
}
