@use '_mixin' as m;

.coins {
  display: flex;
  flex-direction: column;
  gap: m.toRem(10);
  // .coins__header

  &__header {
  }

  // .coins__filters

  &__filters {
    padding: m.toRem(12) m.toRem(20);

    background: #fff;
    border-radius: 6px;
  }

  // .coins__info

  &__info {
    display: flex;
    gap: m.toRem(8);
    @media (max-width: m.toEm(1250)) {
      flex-wrap: wrap;
    }
  }

  &__stats {
    padding: m.toRem(12) m.toRem(20);
    flex: 0 1 min(100%, m.toRem(540));

    background: #fff;
    border-radius: 6px;
  }
  &__chart {
  }
}
.coins-list {
  padding: m.toRem(5) m.toRem(20);
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: m.toRem(6);
  background: #fff;
  border-radius: 6px;

  // .coins-list__item

  &__item {
    display: flex;
  }

  // .coins-list__btn

  &__btn {
    padding: m.toRem(7) m.toRem(10);

    display: flex;
    align-items: center;
    justify-content: center;
    background: #f2f4f7;
    border-radius: 6px;
    color: #1f2226;
    font-size: m.toRem(12);
    transition: background 0.3s ease, color 0.3s;
    @media (any-hover: hover) {
      &:hover {
        background-color: var(--main-color);
        color: var(--white);
      }
    }

    &--active {
      background-color: var(--main-color);
      color: var(--white);
    }
  }
}

.coins-market {
  display: flex;
  flex-direction: column;
  gap: m.toRem(30);

  // .coins-market__item

  &__item {
    display: flex;
    flex-direction: column;
    gap: m.toRem(5);
  }

  // .coins-market__label

  &__label {
    font-weight: 500;
    font-size: m.toRem(14);
  }

  // .coins-market__value

  &__value {
    font-weight: 700;
    font-size: m.toRem(20);
    @media (max-width: m.toEm(768)) {
      font-size: m.toRem(16);
    }
  }

  // .coins-market__change

  &__change {
    display: flex;
    gap: m.toRem(15);
    justify-content: space-between;
    align-items: end;
  }

  // .coins-market__btn

  &__btn {
    display: flex;
    align-items: center;
    gap: m.toRem(6);
  }

  // .coins-market__btn-icon

  &__btn-icon {
    width: m.toRem(16);
    height: m.toRem(16);
  }

  // .coins-market__percent

  &__percent {
    font-weight: 600;
    font-size: m.toRem(16);
    text-align: end;
  }

  // .coins-market__percent--positive

  &__percent--positive {
    color: #3fb84f;
  }
}

.chart {
  padding: m.toRem(12) m.toRem(20);

  background: #fff;
  border-radius: 6px;
  // .chart__header

  &__header {
    margin-bottom: m.toRem(20);
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    gap: m.toRem(10);
  }

  // .chart__left

  &__left {
  }

  &__title {
    font-weight: 600;
    color: #0c0c0c;
  }

  // .chart__btn

  &__btn {
    padding: m.toRem(9) m.toRem(16);
    color: #bababa;
    font-size: m.toRem(14);
  }

  // .chart__right

  &__right {
  }
}

.tabs {
}

.tabs__nav {
  display: flex;
  gap: m.toRem(24);
}

.tabs__button {
  padding-bottom: m.toRem(5);
  color: #bababa;
  font-size: m.toRem(14);
  font-weight: 500;
  border-bottom: 1px solid transparent;
  transition: border 0.3s ease 0s, color 0.3s ease;
  @media (any-hover: hover) {
    &:hover {
      border-bottom: 1px solid #000;
      color: var(--main-color);
    }
  }

  &.active {
    border-bottom: 1px solid #000;
    color: var(--main-color);
  }
}

.tabs__content {
  padding-top: m.toRem(24);
  display: none;
}

.tabs__content.active {
  display: block;
}

.coins-stats {
  // .coins-stats__header

  &__header {
    margin-bottom: m.toRem(24);
    display: flex;
    align-items: center;
    gap: m.toRem(6);
  }

  // .coins-stats__icon

  &__icon {
    width: m.toRem(32);
    height: m.toRem(32);
  }

  // .coins-stats__name

  &__name {
    font-weight: 700;
    font-size: m.toRem(20);
  }

  // .coins-stats__market

  &__market {
    margin-bottom: m.toRem(20);
    display: flex;
    flex-direction: column;
    gap: m.toRem(7);
  }

  // .coins-stats__label

  &__label {
    font-weight: 500;
    font-size: m.toRem(14);
  }

  // .coins-stats__value

  &__value {
    font-weight: 700;
    font-size: m.toRem(20);
  }

  // .coins-stats__list

  &__list {
    display: flex;
    flex-direction: column;
    gap: m.toRem(20);
  }

  // .coins-stats__item

  &__item {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  // .coins-stats__item-text

  &__item-text {
    font-weight: 500;
    font-size: m.toRem(14);
  }

  // .coins-stats__item-value

  &__item-value {
    font-weight: 600;
    font-size: m.toRem(16);
  }

  // .coins-stats__item-label

  &__item-label {
    margin-bottom: m.toRem(5);
    width: 100%;
    font-size: m.toRem(12);
  }
}

.coins-info {
  display: flex;
  flex-direction: column;
  gap: m.toRem(20);

  // .coins-info__item

  &__item {
    display: flex;
    flex-direction: column;
    gap: m.toRem(5);
  }

  // .coins-info__title

  &__title {
    font-size: m.toRem(12);
  }

  // .coins-info__text

  &__text {
    font-weight: 500;
    line-height: 120%;
    font-size: m.toRem(14);
  }

  // .coins-info__link

  &__link {
    font-weight: 500;
    font-size: m.toRem(14);

    line-height: 120%;
    text-decoration: underline;
    text-decoration-skip-ink: none;
    @media (any-hover: hover) {
      &:hover {
        text-decoration: none;
      }
    }
  }
}

.coins-links {
  display: flex;
  flex-wrap: wrap;
  gap: m.toRem(10);
  align-items: center;
  // .coins-links__item

  &__item {
  }

  // .coins-links__btn

  &__btn {
    padding: m.toRem(4) m.toRem(10);
    min-height: m.toRem(24);
    display: flex;
    align-items: center;
    gap: m.toRem(6);
    border: 1px solid #262626;
    border-radius: 100px;
    transition: background 0.3s ease 0s;
    @media (any-hover: hover) {
      &:hover {
        background-color: #f2f2f2;
      }
    }
  }

  // .coins-links__text

  &__text {
    font-size: m.toRem(12);
  }

  // .coins-links__icon

  &__icon {
    width: m.toRem(12);
    height: m.toRem(12);
  }
}
